body {
    font: 14px "Century Gothic", Futura, sans-serif;
    margin: 20px;
    background-color: #18a3ac;
}

ol,
ul {
    padding-left: 30px;
}

h1 {
    margin-top: 150px;
    font-size: 30px;
    color: #fff;
}
.game-board {
    display: flex;
    justify-content: center;
    width: 400px;
    height: 300px;
    margin-top: 80px;
}

.game-area {
    display: flex;
    justify-content: space-around;
}

.board-row:after {
    clear: both;
    content: "";
    display: table;
    
}

.status {
    width: 205px;
    margin-bottom: 10px;
    color: #fff;
    text-align: center;
    font-size: 18px;
    font-weight: 600;
}

.history-btn {
    background-color: #18a3ac;
    color: #fff;
    border: 2px solid #fff;
    width: 140px;
    height: 30px;
    margin-left: 0;
    transition: 0.2s;
}

.history-btn:hover {
    box-shadow: 0 0 15px 2px #fff;
}

.square {
    background-color: #18a3ac;
    border: 2px solid #fff;
    float: left;
    font-size: 44px;
    font-weight: bold;
    line-height: 34px;
    height: 80px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 80px;
    color: #fff;
    box-shadow: inset 0 0 15px 3px #fff,
     0 0 15px 2px #fff;
}

.square:focus {
    outline: none;
}

.kbd-navigation .square:focus {
    background: #ddd;
}

.game {
   text-align: center;
    width: 1000px;
    margin: 0 auto;
}

.game-info {
    margin: 80px 200px 0 20px;
    width: 180px;

}

.history-li {
    margin: 10px 0;
    color: #fff;
}